/* App.css - Versi√≥n mejorada */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f0f2f5;
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header */
.app-header {
  text-align: center;
  margin-bottom: 40px;
  padding: 30px 0;
  background: linear-gradient(135deg, #1a73e8 0%, #2962ff 100%);
  color: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(41, 98, 255, 0.15);
}

.app-header h1 {
  font-size: 2.6rem;
  margin-bottom: 15px;
  font-weight: 700;
  letter-spacing: -0.5px;
}

.app-header p {
  font-size: 1.25rem;
  opacity: 0.9;
  max-width: 700px;
  margin: 0 auto;
}

/* Main Content */
.main-content {
  display: flex;
  flex-direction: column;
  gap: 35px;
}

/* Input Section */
.input-section {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease;
}

.input-section:hover {
  transform: translateY(-3px);
}

.tabs {
  display: flex;
  margin-bottom: 25px;
  border-bottom: 1px solid #e6e9f0;
}

.tabs button {
  padding: 12px 24px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.05rem;
  color: #6e7a89;
  position: relative;
  transition: all 0.2s ease;
}

.tabs button:hover {
  color: #1a73e8;
}

.tabs button.active {
  color: #1a73e8;
  font-weight: 600;
}

.tabs button.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #1a73e8;
  border-radius: 3px 3px 0 0;
}

.input-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.url-input input {
  width: 100%;
  padding: 14px 18px;
  border: 2px solid #e0e4e9;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.2s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.02);
}

.url-input input:focus {
  outline: none;
  border-color: #1a73e8;
  box-shadow: 0 0 0 3px rgba(26, 115, 232, 0.15);
}

.file-input {
  position: relative;
}

.file-label {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 18px;
  background-color: #f5f7fa;
  border: 2px dashed #cad1dc;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #5f6b7a;
  transition: all 0.2s ease;
}

.file-label:hover {
  border-color: #1a73e8;
  background-color: #f0f7ff;
  color: #1a73e8;
}

.file-label::before {
  content: 'üìÑ ';
  font-size: 1.2rem;
  margin-right: 8px;
}

.file-input input[type="file"] {
  position: absolute;
  left: -9999px;
}

.generate-button {
  padding: 14px 28px;
  background: linear-gradient(135deg, #1a73e8 0%, #2962ff 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.05rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 12px rgba(26, 115, 232, 0.25);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.generate-button:hover {
  box-shadow: 0 6px 15px rgba(26, 115, 232, 0.35);
  transform: translateY(-2px);
}

.generate-button:active {
  transform: translateY(0);
  box-shadow: 0 3px 8px rgba(26, 115, 232, 0.2);
}

.generate-button:disabled {
  background: linear-gradient(135deg, #a1a1a1 0%, #878787 100%);
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

/* Processing Section */
.processing-section {
  text-align: center;
  padding: 50px 0;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.08);
}

.processing-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 5px solid rgba(26, 115, 232, 0.15);
  border-radius: 50%;
  border-top-color: #1a73e8;
  animation: spin 1s cubic-bezier(0.42, 0, 0.58, 1) infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.processing-indicator p {
  font-size: 1.2rem;
  color: #6e7a89;
  max-width: 400px;
  margin: 0 auto;
}

/* Video Section */
.video-section {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.08);
}

.video-section h2 {
  margin-bottom: 25px;
  color: #2c3e50;
  position: relative;
  padding-bottom: 12px;
  font-size: 1.6rem;
}

.video-section h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: #1a73e8;
  border-radius: 3px;
}

.video-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.video-container img {
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.video-controls {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.control-button {
  padding: 10px 18px;
  background-color: #f5f7fa;
  border: 1px solid #e0e4e9;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.95rem;
  color: #495057;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.control-button:hover {
  background-color: #e9f0fe;
  border-color: #1a73e8;
  color: #1a73e8;
}

.control-button:nth-child(1)::before {
  content: '‚ñ∂Ô∏è ';
  margin-right: 5px;
}

.control-button:nth-child(2)::before {
  content: '‚¨áÔ∏è ';
  margin-right: 5px;
}

.control-button:nth-child(3)::before {
  content: 'üîó ';
  margin-right: 5px;
}

/* Recent Videos */
.recent-videos {
  background-color: white;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.08);
}

.recent-videos h2 {
  margin-bottom: 25px;
  color: #2c3e50;
  position: relative;
  padding-bottom: 12px;
  font-size: 1.6rem;
}

.recent-videos h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: #1a73e8;
  border-radius: 3px;
}

.video-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 25px;
}

.video-card {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.07);
  transition: all 0.3s ease;
  background-color: white;
  border: 1px solid #e6e9f0;
}

.video-card:hover {
  transform: translateY(-7px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
  border-color: #c0d5ff;
}

.thumbnail {
  height: 160px;
  overflow: hidden;
  position: relative;
}

.thumbnail::after {
  content: '‚ñ∂Ô∏è';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.thumbnail:hover::after {
  opacity: 1;
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.thumbnail:hover img {
  transform: scale(1.05);
}

.video-info {
  padding: 18px;
}

.video-info h3 {
  margin-bottom: 8px;
  font-size: 1.05rem;
  color: #2c3e50;
  transition: color 0.2s ease;
}

.video-card:hover .video-info h3 {
  color: #1a73e8;
}

.date {
  font-size: 0.85rem;
  color: #6e7a89;
  display: flex;
  align-items: center;
}

.date::before {
  content: 'üóìÔ∏è ';
  margin-right: 5px;
}

/* Footer */
.app-footer {
  margin-top: 60px;
  text-align: center;
  padding: 25px 0;
  border-top: 1px solid #e0e4e9;
  color: #6e7a89;
  font-size: 0.95rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .video-grid {
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  }
  
  .app-header h1 {
    font-size: 2.2rem;
  }
  
  .app-header p {
    font-size: 1.1rem;
  }
  
  .input-section, .video-section, .recent-videos {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .video-grid {
    grid-template-columns: 1fr;
  }
  
  .tabs button {
    padding: 10px 15px;
    font-size: 0.95rem;
  }
  
  .app-header h1 {
    font-size: 1.9rem;
  }
  
  .app-header p {
    font-size: 1rem;
  }
  
  .generate-button {
    padding: 12px 20px;
    font-size: 0.95rem;
  }
  
  .video-controls {
    flex-direction: column;
    width: 100%;
  }
  
  .control-button {
    width: 100%;
  }
}

/* A√±ade estos estilos a tu App.css */

.media-input {
  display: flex;
  flex-direction: column;
  gap: 15px;
}


.media-upload-area {
  position: relative;
}

.media-label {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 25px 18px;
  background-color: #f5f7fa;
  border: 2px dashed #cad1dc;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  width: 100%;
  color: #5f6b7a;
  transition: all 0.2s ease;
  min-height: 120px;
  text-align: center;
}

.media-label:hover {
  border-color: #1a73e8;
  background-color: #f0f7ff;
  color: #1a73e8;
}

.media-label::before {
  content: 'üìÅ ';
  font-size: 1.5rem;
  margin-right: 8px;
}

.media-upload-area input[type="file"] {
  position: absolute;
  left: -9999px;
}

.selected-media-preview {
  background-color: #f5f7fa;
  border-radius: 8px;
  padding: 15px;
  margin-top: 10px;
}

.selected-media-preview h4 {
  margin-bottom: 12px;
  color: #2c3e50;
  font-size: 1rem;
}

.media-preview-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 12px;
}

.media-preview-item {
  display: flex;
  flex-direction: column;
  background-color: white;
  border: 1px solid #e0e4e9;
  border-radius: 6px;
  padding: 10px;
  position: relative;
  transition: transform 0.2s;
}

.media-preview-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
}

.media-preview-icon {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 8px;
}

.media-name {
  font-size: 0.85rem;
  color: #495057;
  word-break: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.remove-media {
  position: absolute;
  top: -8px;
  right: -8px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #ff4d4f;
  color: white;
  border: none;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.2s;
}

.media-preview-item:hover .remove-media {
  opacity: 1;
}

.remove-media:hover {
  background-color: #ff7875;
}

